.content__title {
  color: #014a5d;
  font-family: sans-serif;
  font-size: 30px;
}

.content__text {
  color: #2d2d2d;
  line-height: 1.5;
  font-size: 16px;
}

.field {
  margin: 20px 0 0 0;
}

.field__label,
.field__message {
  display: block;
  font-size: 16px;
}

.field__label {
  color: #000000;
  font-weight: bold;
  margin-bottom: 5px;
}

.field_error .field__label {
  color: #ee4f15;
}

.field__input {
  font-size: 16px;
  box-sizing: border-box;
  border: 1px solid #989a9a;
  border-radius: 2px;
  background: #ffffff;
  width: 100%;
  padding: 13px 10px;
}

.field_error .field__input {
  border: 1px solid #ee4f15;
  background: #fef4f4;
}

.field_error input+span:after {
  content: "!";
  margin-left: -30px;
  background: #fcd9d9;;
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  text-align: center;
  color: #ee4f15;
}

.field__input::-webkit-input-placeholder {
  font-size: 16px;
  color: #989a9a;
}

.field__input::-ms-input-placeholder {
  font-size: 16px;
  color: #989a9a;
}

.field__input::placeholder {
  font-size: 16px;
  color: #989a9a;
}

.field__message {
  color: #ee4f15;
  margin-top: 5px;
}

.checkbox {
  margin: 20px 0 0 0;
}

.checkbox__label {
  font-size: 16px;
  color: #000000;
  margin-left: 5px;
}

.checkbox_error .checkbox__label {
  color: #ee4f15;
}

.checkbox__input {
  border: 1px solid #989a9a;
  border-radius: 2px;
  background: #ffffff;
}

.checkbox_error .checkbox__input {
  border: 1px solid #ee4f15;
  background: #fef4f4;
}

.checkbox__message {
  display: block;
  font-size: 16px;
  color: #ee4f15;
  margin-top: 5px;
}

.form-button {
  height: 46px;
  border-radius: 23px;
  background-image: linear-gradient(to right, #ed8402 , #ee4f15);
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 16px;
  margin: 20px 0;
  border: none;
  padding: 0 30px;
}

.form-button::after {
  content: "\203A";
  padding-left: 15px;
  font-size: 18px;
}

.form-button:focus {
  outline: none;
  box-shadow: 0px 0px 2px 3px rgba(255, 51, 0, 0.2);
}

.name-fields {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 1rem;
}
.login {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-gap: 2rem;
}
.header {
  height: 50px;
  width: 100%;
  background-color: #FFFFFF;
  border-bottom: 1px solid #EEEEEE;
}

.header__logo {
  height: 70px;
}
.main {
  margin: 35px auto 0;
  max-width: 1024px;
  padding: 0 10px;
}
html {
  box-sizing: border-box;
  background: #f6f8fb;
  font-family: sans-serif;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

